import React, { useRef } from "react";
import { Modal } from "react-bootstrap";
import { Button, ModalFooter, Table } from "reactstrap";
import PropTypes from 'prop-types'
import { faPrint } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { printDiv } from "../../utils/helper";


const OrderDetailModal = ({ modalIsOpen, modalHandler, selectedOrder: { id, order_date, delivery_date, total_amount, status, order_items } }) => {

    return (
        <Modal
            size="xl"
            dialogClassName="modal-90w"
            show={modalIsOpen}
            backdrop="static"
            scrollable={true}
            onHide={() => modalHandler()}
            id='order-detail-wrap'
        >
            <Modal.Header closeButton>
                <Modal.Title>অর্ডারের বিবরণী</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">আইডি</th>
                            <th>অর্ডারের তারিখ</th>
                            <th>বিতরণ তারিখ</th>
                            <th>মোট পণ্য</th>
                            <th>মোট দাম</th>
                            <th>অর্ডারের অবস্থা</th>
                            <th width="10" className='not-for-print'>কর্ম</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr key={id}>
                            <td>{id}</td>
                            <td>{order_date}</td>
                            <td>{delivery_date}</td>
                            <td>{order_items ? order_items.length : 0}</td>
                            <td>{total_amount}</td>
                            <td>{status}</td>
                            <td className='not-for-print'>
                                <Button color="dark" size="sm" onClick={() => printDiv('order-detail-wrap')}>
                                    <FontAwesomeIcon icon={faPrint} />
                                </Button>
                            </td>
                        </tr>
                    </tbody>
                </Table>

                <hr />
                <h5>পণ্যের বিবরণ</h5>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">নং</th>
                            <th>কোড</th>
                            <th>পণ্যের নাম</th>
                            <th>ইউনিট</th>
                            <th>আনুমানিক হার (টাকা)</th>
                            <th>পরিমাণ</th>
                            <th>মোট (টাকা)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {order_items && order_items.length > 0 && order_items.map((item, i) => <tr key={item.id}>
                            <td>{i + 1}</td>
                            <td>{item.product.product_code}</td>
                            <td>{item.product.name}</td>
                            <td>{item.unit}</td>
                            <td>{item.price}</td>
                            <td>{item.quantity}</td>
                            <td>{item.amount}</td>
                        </tr>)}
                    </tbody>
                </Table>
            </Modal.Body>
            <ModalFooter className='not-for-print'>
                <Button color="secondary" onClick={() => modalHandler()}>পেছনে</Button>
            </ModalFooter>

        </Modal>
    )
}


OrderDetailModal.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    selectedOrder: PropTypes.object.isRequired,
    modalIsOpen: PropTypes.bool,
}

export default OrderDetailModal;
