import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, FormGroup, Input, Label, FormFeedback } from "reactstrap";
import { Authenticate } from "../../services";
import PropTypes from 'prop-types'
import { changePasswordValidator } from "../../validators";



let initState = {
    password: '',
    confirm_password: '',
    error: {},
    isLoading: false
}



const authenticate = new Authenticate()

class ChangePassword extends Component {
    state = initState


    changeHandler = (e) => {
        this.setState(prevState => {
            let { error } = prevState

            if (error[e.target.name])
                delete error[e.target.name]

            return {
                [e.target.name]: e.target.value,
                error
            }
        });
    };


    submitHandler = async (e) => {
        e.preventDefault();
        let validate = changePasswordValidator(this.state)
        if (!validate.isValid) {
            this.setState({ error: validate.error })
            return;
        }

        this.setState({ isLoading: true })

        let response = await authenticate.changePassword(this.state)
        if (response) {
            this.setState(initState)
            this.props.modalHandler()
            return;
        }
        this.setState({ isLoading: false })
    }


    render() {
        let { editIsOpen, modalHandler } = this.props;
        let { password, confirm_password, error, isLoading } = this.state


        return (
            <Modal
                size="md"
                dialogClassName="modal-90w"
                show={editIsOpen}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>পাসওয়ার্ড পরিবর্তন</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={this.submitHandler}>
                        <FormGroup>
                            <Label for="password">
                                পাসওয়ার্ড<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="password"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="password"
                                value={password}
                                required
                                invalid={error.password ? true : false}
                            />
                            {error.password && <FormFeedback>{error.password}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="confirm_password">
                                কনফার্ম পাসওয়ার্ড<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="password"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="confirm_password"
                                value={confirm_password}
                                required
                                invalid={error.confirm_password ? true : false}
                            />
                            {error.confirm_password && <FormFeedback>{error.confirm_password}</FormFeedback>}
                        </FormGroup>
                        <Button
                            color=""
                            className="site-btn-dark"
                            block
                            type="submit"
                            disabled={isLoading}
                        >
                            {isLoading ? "Please Wait ..." : "Submit"}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        )
    }
}


ChangePassword.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    editIsOpen: PropTypes.bool,
}


export default ChangePassword;
