import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, FormGroup, Input, Label, FormFeedback } from "reactstrap";
import { Authenticate } from "../../services";
import PropTypes from 'prop-types'
import { profileValidator } from "../../validators";



let initState = {
    name: '',
    email: '',
    phone_number: '',
    address: '',
    error: {},
    isLoading: true
}



const authenticate = new Authenticate()

class EditProfile extends Component {
    state = initState

    componentDidMount() {
        this.setState({
            ...this.props.profile,
            error: {},
            isLoading: false
        })
    }


    changeHandler = (e) => {
        this.setState(prevState => {
            let { error } = prevState

            if (error[e.target.name])
                delete error[e.target.name]

            return {
                [e.target.name]: e.target.value,
                error
            }
        });
    };


    submitHandler = async (e) => {
        e.preventDefault();
        let validate = profileValidator(this.state)
        if (!validate.isValid) {
            this.setState({ error: validate.error })
            return;
        }

        this.setState({ isLoading: true })

        let response = await authenticate.updateProfile(this.state)
        if (response) {
            this.setState(initState)
            this.props.getData()
            return;
        }
        this.setState({ isLoading: false })
    }


    render() {
        let { editIsOpen, modalHandler } = this.props;
        let { name, email, phone_number, address, error, isLoading } = this.state


        return (
            <Modal
                size="md"
                dialogClassName="modal-90w"
                show={editIsOpen}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>তথ্য পরিবর্তন</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={this.submitHandler}>
                        <FormGroup>
                            <Label for="name">
                                নাম<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="name"
                                value={name}
                                required
                                invalid={error.name ? true : false}
                            />
                            {error.name && <FormFeedback>{error.name}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="phone">
                                ফোন নম্বর<span className="text-danger">*</span>
                            </Label>
                            <Input
                                type="text"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="phone_number"
                                value={phone_number}
                                required
                                invalid={error.phone_number ? true : false}
                            />
                            {error.phone_number && <FormFeedback>{error.phone_number}</FormFeedback>}
                        </FormGroup>
                        <FormGroup>
                            <Label for="email">
                                ইমেইল
                            </Label>
                            <Input
                                type="email"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="email"
                                value={email}
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="address">
                                ঠিকানা
                            </Label>
                            <Input
                                type="address"
                                placeholder="Type here..."
                                onChange={this.changeHandler}
                                name="address"
                                value={address}
                            />
                        </FormGroup>
                        <Button
                            color=""
                            className="site-btn-dark"
                            block
                            type="submit"
                            disabled={isLoading}
                        >
                            {isLoading ? "Please Wait ..." : "Submit"}
                        </Button>
                    </Form>
                </Modal.Body>
            </Modal>
        )
    }
}


EditProfile.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    getData: PropTypes.func.isRequired,
    editIsOpen: PropTypes.bool,
    profile: PropTypes.object.isRequired
}


export default EditProfile;
