import React, { Component } from "react";
import { Form, Modal } from "react-bootstrap";
import { Button, ModalFooter, Table } from "reactstrap";
import { ProductAction, OrderAction } from "../../services";
import dateFormat from 'dateformat';
import { dateTime, decimalNumber } from "../../utils/helper";
import PropTypes from 'prop-types'
import confirm from "reactstrap-confirm";




const productAction = new ProductAction()
const orderAction = new OrderAction()

let initState = {
    isLoading: true,
    deliveryDate: ' ',
    products: [],
    id: null
}

class OrderPlaceModal extends Component {
    state = initState

    async componentDidMount() {
        let { data } = await productAction.get()
        if (!Object.keys(data).length) {
            this.setState({
                isLoading: false,
            })
            return;
        }


        if (Object.keys(this.props.editData).length) {
            let { id, order_items, delivery_date } = this.props.editData
            let products = []
            order_items.map(item => {
                let pdt = {
                    ...item.product,
                    quantity: item.quantity
                }
                products.push(pdt)
                return true;
            })
            this.setState({ isLoading: false, products, deliveryDate: delivery_date, id })
        } else {
            this.setState({ isLoading: false, products: data.results })
        }
    }

    changeHandler(e, id) {
        let input = parseInt(e.target.value)
        if (input < 0)
            return null

        this.setState(prevState => {
            let { products } = prevState

            let pdt = products.map(item => {
                let quantity = item.quantity || 0
                if (item.id === id) {
                    quantity = e.target.value
                }

                return {
                    ...item,
                    quantity: parseInt(quantity)
                }
            })
            return {
                products: pdt
            }
        })
    }


    submitHandler = async () => {
        let { products, deliveryDate, id } = this.state

        let order_items = []
        products.map(item => {
            if (parseInt(item.quantity) > 0) {
                let pdt = {
                    product: item.id,
                    price: item.unit_rate,
                    unit: item.unit,
                    quantity: decimalNumber(item.quantity),
                }
                order_items.push(pdt)
            }
            return true;
        })


        if (order_items.length === 0) {
            alert('অর্ডার দেওয়ার জন্য অন্তত একটি পণ্য নির্বাচন করুন!')
            return null;
        } else if (!deliveryDate || deliveryDate === '' || deliveryDate === ' ') {
            alert('ডেলিভারির তারিখ নির্বাচন করুন!')
            return null;
        }


        let formData = {
            order_items,
            delivery_date: dateFormat(deliveryDate, "yyyy-mm-dd"),
            order_date: dateFormat(new Date(), "yyyy-mm-dd")
        }
        let response
        if (id) {
            response = await orderAction.updateOrder(id, formData)
        } else {
            response = await orderAction.submitOrder(formData)
        }

        if (response.isSuccess) {
            this.props.getData()
            this.setState(initState)
        } else {
            this.setState({ isLoading: false })
        }
    }


    dateChangeHandler = e => {
        if (dateTime() >= e.target.value) {
            alert('একটি বৈধ তারিখ নির্বাচন করুন!!')
            return;
        }
        this.setState({
            deliveryDate: e.target.value
        })
    }


    render() {
        let { modalIsOpen, modalHandler } = this.props;
        let { products, deliveryDate, isLoading, id } = this.state

        const getTotalPrice = () => {
            let totalPrice = 0
            if (products.length) {
                products.map(item => {
                    let quantity = parseInt(item.quantity) || 0
                    let price = parseInt(item.unit_rate)

                    if (quantity > 0) {
                        totalPrice += price * quantity
                    }
                    return true;
                })
            }

            return totalPrice;
        }

        return (
            <Modal
                size="xl"
                dialogClassName="modal-90w"
                show={modalIsOpen}
                backdrop="static"
                scrollable={true}
                onHide={() => modalHandler()}
            >
                <Modal.Header closeButton>
                    <Modal.Title>তালিকা থেকে পণ্য নির্বাচন করুন</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Table responsive striped bordered hover size="sm" className="text-center">
                        <thead>
                            <tr>
                                <th width="15">নং</th>
                                <th>কোড</th>
                                <th>পণ্যের নাম</th>
                                <th>ইউনিট</th>
                                <th>আনুমানিক হার (টাকা)</th>
                                <th>পরিমাণ</th>
                                <th>মোট (টাকা)</th>
                            </tr>
                        </thead>
                        <tbody>
                            {isLoading ? <tr>
                                <td colSpan={10}>Loading...</td>
                            </tr> : products.length > 0 && products.map((item, i) => <tr key={item.id}>
                                <td>{i + 1}</td>
                                <td>{item.product_code}</td>
                                <td>{item.name}</td>
                                <td>{item.unit}</td>
                                <td>{item.unit_rate}</td>
                                <td width="100">
                                    <Form.Control
                                        type="number"
                                        size="sm"
                                        name="quantity"
                                        value={item.quantity || 0}
                                        onChange={(e) => this.changeHandler(e, item.id)}
                                    /></td>
                                <td>{parseInt(item.unit_rate) * parseInt(item.quantity || 1)}</td>
                            </tr>)}

                            <tr>
                                <td colSpan={2}>ডেলিভারির তারিখ:<span className="text-danger">*</span></td>
                                <td colSpan={1}>
                                    <Form.Control
                                        type="date"
                                        size="sm"
                                        name="dateTo"
                                        value={deliveryDate}
                                        required
                                        onChange={this.dateChangeHandler}
                                    // onChange={(e) => this.setState({ deliveryDate: e.target.value })}
                                    />
                                </td>
                                <td colSpan={4}>সর্বমোট: {getTotalPrice()}</td>
                            </tr>
                            <tr>
                                <td colSpan={10} className='text-left'><b>দ্রষ্টব্য: দাম বিভিন্ন হতে পারে</b></td>
                            </tr>
                        </tbody>
                    </Table>
                </Modal.Body>
                <ModalFooter>
                    <Button
                        color="warning"
                        onClick={async () => await confirm({ title: 'অর্ডার জমা দিন', message: "আপনি কি অর্ডারটি জমা দেওয়ার বিষয়ে নিশ্চিত?", confirmText: "হ্যাঁ", cancelText: "না" }) && this.submitHandler()}
                        disabled={isLoading}
                    >{id ? 'অর্ডার পরিবর্তন ' : 'অর্ডার করুন '}</Button>{' '}
                    <Button color="secondary" onClick={() => modalHandler()} disabled={isLoading}>পেছনে</Button>
                </ModalFooter>
            </Modal>
        )
    }
}


OrderPlaceModal.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    getData: PropTypes.func.isRequired,
    modalIsOpen: PropTypes.bool,
}


export default OrderPlaceModal;
