import React, { Component } from 'react';
import { Container, Card } from 'react-bootstrap';
import { Authenticate } from '../../services';
import { Spinner, Button } from 'reactstrap';
import EditProfile from './EditProfile';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPenAlt, faUnlock } from "@fortawesome/free-solid-svg-icons";
import ChangePassword from './ChangePassword';



const authenticate = new Authenticate()
class Profile extends Component {
    state = {
        profile: {},
        isLoading: true,
        editIsOpen: false,
        changePassIsOpen: false
    }

    componentDidMount() {
        this.getData()
    }


    getData = async () => {
        try {
            this.setState({
                isLoading: true,
                editIsOpen: false,
                changePassIsOpen: false
            })

            let { data: { data } } = await authenticate.getProfile()
            this.setState({
                profile: data,
                isLoading: false
            })
        } catch (error) {
            console.error(error)
        }
    }

    render() {
        let { profile, isLoading, editIsOpen, changePassIsOpen } = this.state

        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <h5><u>একাউন্ট তথ্যাবলী</u></h5>


                        <Button
                            className="float-right ml-4"
                            type="button"
                            disabled={isLoading}
                            onClick={() => this.setState({ changePassIsOpen: true })}
                        >
                            <FontAwesomeIcon icon={faUnlock} /> পাসওয়ার্ড পরিবর্তন
                        </Button>

                        <Button
                            className="float-right"
                            type="button"
                            disabled={isLoading}
                            onClick={() => this.setState({ editIsOpen: true })}
                        >
                            <FontAwesomeIcon icon={faPenAlt} /> তথ্য পরিবর্তন
                        </Button>
                    </Card.Header>
                    <Card.Body>
                        {isLoading ? <Spinner color="dark" />
                            : <div>
                                <h5>নাম: {profile.name}</h5>
                                <h5>ফোন নম্বর: {profile.phone_number}</h5>
                                <h5>ইমেইল: {profile.email}</h5>
                                <h5>ঠিকানা: {profile.address}</h5>
                            </div>}
                    </Card.Body>
                </Card>


                {editIsOpen &&
                    <EditProfile
                        getData={this.getData}
                        editIsOpen={editIsOpen}
                        profile={profile}
                        modalHandler={() => this.setState({ editIsOpen: false })}
                    />}

                {changePassIsOpen &&
                    <ChangePassword
                        editIsOpen={changePassIsOpen}
                        modalHandler={() => this.setState({ changePassIsOpen: false })}
                    />}


            </Container>
        );
    }
}

export default Profile;
