import React from 'react';
import { Table, Button } from 'reactstrap';
import PropTypes from 'prop-types'
import confirm from "reactstrap-confirm";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMoneyBillAlt } from "@fortawesome/free-solid-svg-icons";



const DataTable = ({ isLoading, list, paymentHandler }) => {
    return (
        <Table>
            <thead>
                <tr>
                    <th>আইডি</th>
                    <th>অর্ডার নং</th>
                    <th>মোট টাকা</th>
                    <th>অবস্থা</th>
                    <th>কর্ম</th>
                </tr>
            </thead>
            <tbody>
                {isLoading ? <tr>
                    <td colSpan={10}>Loading...</td>
                </tr> : list.length === 0 ? <tr>তালিকায় কোনও আইটেম নেই।</tr> : list.map(item => <tr key={item.id}>
                    <td>{item.id}</td>
                    <td>{item.order.order_no}</td>
                    <td>{item.amount}</td>
                    <td>{item.status}</td>
                    <td>
                        {item.status === 'Unpaid' &&
                            <Button
                                color="warning"
                                size="sm"
                                className="mr-2"
                                onClick={async () => await confirm({ title: 'পেমেন্ট নিশ্চিত করা', message: "আপনি কি নিশ্চিত ভাবে পেমেন্ট করতে চান?", confirmText: "হ্যাঁ", cancelText: "না" }) && paymentHandler(item.id)}
                            >
                                <FontAwesomeIcon icon={faMoneyBillAlt} />
                            </Button>}
                    </td>
                </tr>)}
            </tbody>
        </Table>
    );
}

DataTable.propTypes = {
    isLoading: PropTypes.bool,
    list: PropTypes.array,
    paymentHandler: PropTypes.func.isRequired,
}

export default DataTable;
