import React, { Component } from 'react'
import { Table, Button } from 'reactstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faPenAlt, faCheck, faTimes } from "@fortawesome/free-solid-svg-icons";
import OrderDetailModal from './OrderDetailModal';
import PropTypes from 'prop-types'
import confirm from "reactstrap-confirm";




class OrderHistory extends Component {
    state = {
        selectedOrder: {},
        modalIsOpen: false
    }


    render() {
        let { orders, isLoading, editHandler, orderApproveCancelHandler } = this.props
        let { selectedOrder, modalIsOpen } = this.state

        return (
            <>
                <Table responsive striped bordered hover size="sm" className="text-center order-history">
                    <thead>
                        <tr>
                            <th width="15">নং</th>
                            <th>অর্ডারের তারিখ</th>
                            <th>বিতরণ তারিখ</th>
                            <th>মোট পণ্য</th>
                            <th>মোট দাম</th>
                            <th>অর্ডারের অবস্থা</th>
                            <th width="10">কর্ম</th>
                        </tr>
                    </thead>
                    <tbody>
                        {isLoading ? <tr>
                            <td colSpan={10}>Loading...</td>
                        </tr> : orders.length > 0 && orders.map((item, i) => <tr key={item.id}>
                            <td>{i + 1}</td>
                            <td>{item.order_date}</td>
                            <td>{item.delivery_date}</td>
                            <td>{item.order_items.length || 0}</td>
                            <td>{item.total_amount}</td>
                            {/* <td>{item.total_amount}</td> */}
                            <td>{item.status}</td>
                            <td>
                                <p>
                                    <Button
                                        color="warning"
                                        size="sm"
                                        className="mr-2"
                                        onClick={() => this.setState({ selectedOrder: item, modalIsOpen: true })}>
                                        <FontAwesomeIcon icon={faEye} />
                                    </Button>

                                    {item.status === 'Pending' &&
                                        <Button
                                            color="primary"
                                            size="sm"
                                            className="mr-2"
                                            onClick={async () => await confirm({ title: 'অর্ডার নিশ্চিত করা', message: "আপনি কি নিশ্চিত যে আপনি অর্ডারটি নিশ্চিত করতে চান?", confirmText: "হ্যাঁ", cancelText: "না" }) && orderApproveCancelHandler(item.id, 'confirmed')}
                                        >
                                            <FontAwesomeIcon icon={faCheck} />
                                        </Button>
                                    }

                                    {item.status === 'Proposed' &&
                                        <Button
                                            color="dark"
                                            size="sm"
                                            className="mr-2"
                                            onClick={() => editHandler(item)}>
                                            <FontAwesomeIcon icon={faPenAlt} />
                                        </Button>
                                    }



                                    {(item.status === 'Proposed' || item.status === 'Pending') &&
                                        <Button
                                            color="danger"
                                            size="sm"
                                            onClick={async () => await confirm({ title: 'অর্ডার বাতিল নিশ্চিত করা', message: "আপনি কি নিশ্চিত যে আপনি অর্ডারটি বাতিল করতে চান?", confirmText: "হ্যাঁ", cancelText: "না" }) && orderApproveCancelHandler(item.id, 'cancel')}
                                        >
                                            <FontAwesomeIcon icon={faTimes} />
                                        </Button>
                                    }
                                </p>
                            </td>
                        </tr>)}
                        {!isLoading && orders.length === 0 &&
                            <tr><td colSpan={10}>তালিকায় কোনও আইটেম নেই।</td></tr>}
                    </tbody>
                </Table>
                <OrderDetailModal
                    modalIsOpen={modalIsOpen}
                    selectedOrder={selectedOrder}
                    modalHandler={() => this.setState({ selectedOrder: {}, modalIsOpen: false })}
                />
            </>
        )
    }
}


OrderHistory.propTypes = {
    order: PropTypes.array,
    editHandler: PropTypes.func.isRequired,
    orderApproveCancelHandler: PropTypes.func.isRequired,
}

export default OrderHistory