import React, { Component } from 'react';
import { BillingAction } from '../../services';
import { Card, Container } from "react-bootstrap";
import DataTable from './DataTable'


const billingAction = new BillingAction()
class Billing extends Component {
    state = {
        list: []
    }

    componentDidMount() {
        this.getData()
    }
    getData = async () => {
        this.setState({
            isLoading: true
        })

        try {
            let { data: { results } } = await billingAction.get()
            this.setState({
                list: results,
                isLoading: false
            })
        } catch (error) {
            console.error(error)
        }
    }


    paymentHandler = async (id) => {
        try {
            await billingAction.pay(id)
            this.getData()
        } catch (error) {
            console.error(error)
        }
    }

    render() {
        let { list, isLoading } = this.state

        return (
            <Container fluid={true} style={{ marginTop: 95 }}>
                <Card>
                    <Card.Header>
                        <h5><u>পেমেন্ট লিস্ট</u></h5>
                    </Card.Header>
                    <Card.Body>
                        <DataTable
                            list={list}
                            isLoading={isLoading}
                            paymentHandler={this.paymentHandler}
                        />
                    </Card.Body>
                </Card>
            </Container>
        );
    }
}

export default Billing;
